import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-issue-invoice',
  templateUrl: './issue-invoice.component.html',
  styleUrls: ['./issue-invoice.component.scss']
})
export class IssueInvoiceComponent {



  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
  ) { }



  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki']);
  }



  gotoControlIssueDeliveryGoods() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/issue-invoice/issue-delivery-goods']);
  }



  gotoControlIssueReceiveGoods() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/issue-invoice/issue-receive-goods']);
  }



  /**
   * Đi đến Hóa đơn đã phát hành
   */
  gotoInvoiceIssued() {
    const id_shop = this.vhQuerySales.getlocalShopListTiki()?.[0]?._id;
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/issue-invoice/invoice-issued'], { 
      state: { 
        shop_info: {
          from_type: 'tiki_invoice_issued',
          id_shop: id_shop,
          bill_type: [1],
        } 
      } 
    });
  }



  /**
   * Đi đến Hóa đơn trả về
   */
  gotoInvoiceReturned() {
    const id_shop = this.vhQuerySales.getlocalShopListTiki()?.[0]?._id;
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/issue-invoice/invoice-returned'], { 
      state: { 
        shop_info: {
          from_type: 'tiki_invoice_returned',
          id_shop: id_shop,
          bill_type: [3],
        } 
      } 
    });
  }

}
