import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IssueInvoiceComponent } from './issue-invoice.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: "",
    component: IssueInvoiceComponent
  },
  {
    path: 'issue-delivery-goods',
    loadChildren: () => import('./issue-delivery-goods/issue-delivery-goods.module').then(m => m.IssueDeliveryGoodsModule)
  },
  {
    path: 'issue-receive-goods',
    loadChildren: () => import('./issue-receive-goods/issue-receive-goods.module').then(m => m.IssueReceiveGoodsModule)
  },
  {
    path: 'invoice-returned',
    loadChildren: () => import('../../../invoices/sales-return-invoice/sales-return-invoice.module').then(m => m.SalesReturnInvoiceModule)
  },
  {
    path: 'invoice-issued',
    loadChildren: () => import('../../../invoices/sales-return-invoice/sales-return-invoice.module').then(m => m.SalesReturnInvoiceModule)
  },
]

@NgModule({
  declarations: [
    IssueInvoiceComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class IssueInvoiceModule { }
